body{
  padding-top: 60px;
}
header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 60px;
    background-color: rgb(80, 77, 228);
    padding: 0 20px;
    font-family: Arial, sans-serif;
    font-size: 16px;
    color: white;
    box-shadow: 0 12px 4px -4px rgb(80 77 228 / 60%);
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 9999;
  }
  
  nav {
    display: flex;
    align-items: center;
    flex-grow: 1;
    text-align: center;
  }
  
  /* Стили для иконки меню */
  .menu_icon {
    width: 30px;
    height: 30px;
    fill: white;
  }
  
  /* Стили для контейнера поиска */
  .search_container {
    position: relative;
    display: flex;
    align-items: center;
    width: 250px; /* Задаем фиксированную ширину */
    margin: 0 auto; /* Центрируем по горизонтали */
  }
  
  .search_input {
    border: none;
    background-color: rgb(66, 63, 197);
    padding: 10px;
    border-radius: 30px;
    font-size: 16px;
    transition: all 0.3s ease;
    box-shadow: 0px 0px 6px 0px rgba(0, 0, 0, 0.3);
    width: 100%;

  }
  
  .search_icon {
    width: 24px;
    height: 24px;
    fill: white;
    position: absolute;
    right: 10px;
    cursor: pointer;
  }
  
  /* Стили для фото пользователя */
  .user_photo {
    width: 40px;
    height: 40px;
    border-radius: 50%;

    border: 2px solid #fff;
    box-shadow: 0px 0px 6px 0px rgba(0, 0, 0, 0.3);
  }
  ::placeholder {
    color: white;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 14px;
    letter-spacing: 0.6px;
  }
  input[type="text"], 
input[type="search"] {
  color: #fff; /* установка цвета текста в белый */
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 14px;
    letter-spacing: 0.6px;
}
