.add_post {
  display: flex;
  align-items: center;
  padding: 5px;
  border: 1px solid white;
  border-radius: 30px;
  cursor: pointer;
  width: 100%;
  justify-content: center;
  background-color: rgb(80, 77, 228);
  box-shadow: 0 1px 3px rgb(0 0 0 / 12%);
  margin-bottom: 10px;
}

.add_post:hover {
  background-color: #e3e3e3;
}

.add_post_text {
  font-size: 14px;
  color: white;
}

.add_post_icon {
  width: 20px;
  height: 20px;
  margin-right: 5px;
  fill: white;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
}

.modal_content {
  background-color: white;
  padding: 20px;
  border-radius: 5px;
  position: absolute;
}

.modal_header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.modal_header h2 {
  margin: 0;
}

.modal_body{
  width: 100%;
  padding: 10px;
  border: 1px solid black;
  border-radius: 5px;
  font-size: 16px;
}
